import { BadRequestException, Injectable, Logger } from '@nestjs/common';

import { Client, LocalAuth } from "whatsapp-web.js";
import { SendMessage } from './instace.interface';

interface Instace {
    instanceID: string
    instance: Client,
    qr?: string
}

@Injectable()
export class InstanceService {
    private logger = new Logger("Instance")
    private INSTANCES: Instace[] = []

    async createInstance(instanceID: string) {

        if (this.INSTANCES.findIndex(i => i.instanceID == instanceID) !== -1) throw new BadRequestException("Instance already created!")

        this.INSTANCES.push({
            instanceID,
            instance: null
        })

        await this.addInstance(instanceID)

        return {
            "message": `Instance ${instanceID} Created`
        }
    }

    private addInstance(instanceID: string) {
        this.logger.log(`[${instanceID}] is Creating`)

        const instance = new Client({
            authStrategy: new LocalAuth({ clientId: instanceID })
        });
        instance.on('qr', (qr) => {
            this.logger.log(`[${instanceID}] Got QR Code`)
            const instanceIndex = this.INSTANCES.findIndex(i => i.instanceID == instanceID)
            if (instanceIndex == -1) {
                this.logger.error(`[QR] Instance ${instanceID} not Found`)
            }
            this.INSTANCES[instanceIndex].qr = qr
        });

        instance.on('ready', async () => {
            this.logger.log(`[${instanceID}] is Ready`)
            const instanceIndex = this.INSTANCES.findIndex(i => i.instanceID == instanceID)
            if (instanceIndex == -1) {
                this.logger.error(`Instance ${instanceID} not Found`)
            }
            this.INSTANCES[instanceIndex].instance = instance
            this.INSTANCES[instanceIndex].qr = "READY"
            this.logger.log(`[${instanceID}] SAVED to Global Instances`)
        });

        instance.initialize();

        return instance
    }

    async getQrCode(instanceID: string,) {
        const instace = this.INSTANCES.filter(i => i.instanceID === instanceID)

        if (instace.length == 0) throw new BadRequestException("Instance not found!")

        return {
            qr: instace[0].qr
        }
    }

    private getInstace(instanceID: string) {
        const instace = this.INSTANCES.filter(i => i.instanceID === instanceID)

        if (instace.length == 0) return undefined

        return instace[0].instance
    }

    async destroyInstance(instanceID: string) {
        try {
            await this.addInstance(instanceID).destroy()

            return {
                message: `Instance ${instanceID} has Destroyed!`
            }
        } catch (error) {
            throw new BadRequestException()
        }
    }



    async sendMessage(instanceID: string, message: SendMessage) {
        const instance = this.getInstace(instanceID)

        if (instance == undefined) throw new BadRequestException("Instance not found!")

        const state = await instance.getState()

        if (state == "CONNECTED") {
            const phone = await instance.getNumberId(message.phone)
            return await instance.sendMessage(phone._serialized, message.body)
        }

        throw new BadRequestException("Instance not Connected!")
    }
}
