import { Body, Controller, Get, Param, Post } from '@nestjs/common';

import { InstanceService } from './instance.services';

@Controller('instance')
export class InstanceController {
    constructor(private readonly instanceService: InstanceService) { }

    @Post('create')
    async createInstance(@Body('instanceID') id: string) {
        return this.instanceService.createInstance(id)
    }

    @Post(':instanceID/message')
    async sendMessage(@Body() body: any, @Param('instanceID') instanceID: string) {
        return this.instanceService.sendMessage(instanceID, body)
    }

    @Get(":instanceID/qr")
    async qr(@Param('instanceID') instanceID: string) {
        return this.instanceService.getQrCode(instanceID)
    }
}
